import React, {Component} from 'react';
import {BrowserRouter, Redirect, Route, Switch} from 'react-router-dom';
import {observer, Provider} from "mobx-react";
import {companyDetails, counterparties, dashboard, defaultUrl, equipmentRepairs, invoiceLines, invoices, login, logout, navigation, users} from "../configurations/navigation";
import PrivateRoute from "../base/components/auth/PrivateRoute";
import {injectIntl, IntlProvider} from "react-intl";
import {Helmet, HelmetProvider} from 'react-helmet-async';
import {appTranslations} from "../localizations";
import CounterpartyEditPage from "./counterparties/EditPage";
import CounterpartyCreatePage from "./counterparties/CreatePage";
import MenusLayout from "./shared/MenusLayout";
import CounterpartyListPage from "./counterparties/ListPage";
import {ConfigProvider} from "antd";
import InvoiceListPage from "./invoices/ListPage";
import InvoiceCreatePage from "./invoices/CreatePage";
import InvoiceEditPage from "./invoices/EditPage";
import DashboardPage from "./Dashboard";
import InvoiceLineEditPage from "./invoice-lines/EditPage";
import InvoiceLineCreatePage from "./invoice-lines/CreatePage";
import InvoiceLineListPage from "./invoice-lines/ListPage";
import {antdLocales, intlFormats} from "../base/localizations";
import InvoicePreviewPage from "./invoices/InvoicePreviewPage";
import CompanyDetailsEditPage from "./company-details/EditPage";
import LogoutPage from "../base/components/auth/Logout";
import LoginPage from "./auth/LoginPage";
import UserEditPage from "./users/EditPage";
import UserCreatePage from "./users/CreatePage";
import UserListPage from "./users/ListPage";
import EquipmentRepairEditPage from "./equipment-repairs/EditPage";
import EquipmentRepairCreatePage from "./equipment-repairs/CreatePage";
import EquipmentRepairListPage from "./equipment-repairs/ListPage";

@injectIntl
@observer
class App extends Component {
	constructor(props) {
		super(props);

		props.context.appService.setSidebarMenuItems(navigation);
	}

	render() {
		return <>
			<Helmet>
				<title>{this.props.intl.formatMessage({id: "application.meta.title"})}</title>
			</Helmet>
			<BrowserRouter>
				<Switch>
					<Route exact path={login.href} component={LoginPage}/>
					<PrivateRoute exact path={logout.href} component={LogoutPage}/>
					<PrivateRoute exact path="/" component={Redirect} to={defaultUrl.href}/>

					<PrivateRoute exact path={`${dashboard.href}`} component={DashboardPage} authenticatedLayout={MenusLayout}/>

					<PrivateRoute exact path={`${invoices.href}/:invoiceId([0-9]+)`} component={InvoiceEditPage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${invoices.href}/create`} component={InvoiceCreatePage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${invoices.href}`} component={InvoiceListPage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${invoices.href}/:invoiceId([0-9]+)/preview`} component={InvoicePreviewPage} authenticatedLayout={MenusLayout}/>

					<PrivateRoute exact
								  path={`${invoiceLines.href.replace("{firstLevelParentId}", ":invoiceId([0-9]+)")}/:invoiceLineId([0-9]+)`}
								  component={InvoiceLineEditPage}
								  authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact
								  path={`${invoiceLines.href.replace("{firstLevelParentId}", ":invoiceId([0-9]+)")}/create`}
								  component={InvoiceLineCreatePage}
								  authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact
								  path={`${invoiceLines.href.replace("{firstLevelParentId}", ":invoiceId([0-9]+)")}`}
								  component={InvoiceLineListPage}
								  authenticatedLayout={MenusLayout}/>

					<PrivateRoute exact path={`${counterparties.href}/:counterpartyId([0-9]+)`} component={CounterpartyEditPage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${counterparties.href}/create`} component={CounterpartyCreatePage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${counterparties.href}`} component={CounterpartyListPage} authenticatedLayout={MenusLayout}/>

					<PrivateRoute exact path={`${equipmentRepairs.href}/:equipmentRepairId([0-9]+)`} component={EquipmentRepairEditPage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${equipmentRepairs.href}/create`} component={EquipmentRepairCreatePage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${equipmentRepairs.href}`} component={EquipmentRepairListPage} authenticatedLayout={MenusLayout}/>

					<PrivateRoute exact path={`${users.href}/:userId([0-9]+)`} component={UserEditPage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${users.href}/create`} component={UserCreatePage} authenticatedLayout={MenusLayout}/>
					<PrivateRoute exact path={`${users.href}`} component={UserListPage} authenticatedLayout={MenusLayout}/>

					<PrivateRoute exact path={`${companyDetails.href}`} component={CompanyDetailsEditPage} authenticatedLayout={MenusLayout}/>
				</Switch>
			</BrowserRouter>
		</>;
	}
}

class Application extends Component {
	render() {
		return <IntlProvider
			key={this.props.context.appService.settings.locale}
			locale={this.props.context.appService.settings.locale}
			messages={appTranslations[this.props.context.appService.settings.locale]}
			formats={intlFormats}
		>
			<ConfigProvider locale={antdLocales[this.props.context.appService.settings.locale]}>
				<HelmetProvider>
					<Provider context={this.props.context} {...(this.props.context.services)}>
						<App context={this.props.context}/>
					</Provider>
				</HelmetProvider>
			</ConfigProvider>
		</IntlProvider>
	}

	componentDidCatch(error, errorInfo) {
		console.log(error, errorInfo);
	}
}

export default Application;
